import React, { useState } from "react";
import "./modal.css";
import Dropdown from './dropdown';


// Modal bileeni. Bu bileen bir form ieriyor ve kullanc bilgilerini toplamak iin kullanlyor.
export const Modal = ({ onSubmit, onCancel }) => {
    // Form verilerini ynetmek iin bir state oluturulur.
    const [formData, setFormData] = useState({
        name: '',
        email: '',
        username: '',
        role: '',
        company_name: '',
    });

    // Rol iin dropdown mensnde kullanlacak seenekler
    const roleOptions = [
        { value: "Admin", label: "Admin" },
        { value: "Bakm Grevlisi", label: "Bakm Grevlisi" },
        { value: "Teknisyen", label: "Teknisyen" },
    ];

    // Input alanndaki deiiklikleri yakalayp, formData stateini gncelleyen fonksiyon.
    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setFormData((prevData) => ({
            ...prevData,
            [name]: value, // Deien input alannn deerini formData stateine kaydeder.
        }));
    };

    // Dropdown bileeninde seilen rol formData stateine kaydeden fonksiyon.
    const handleRoleChange = (selectedOption) => {
        setFormData((prevData) => ({
            ...prevData,
            role: selectedOption ? selectedOption.value : '', // Eer bir seenek seilmise seilen deeri rol olarak kaydeder.
        }));
    };

    // Form gnderildiinde alacak olan fonksiyon. Form verilerini onSubmit fonksiyonuna iletir.
    const handleSubmit = () => {
        onSubmit(formData);
    };

    return (
        <div className="modal-container">
            <div className="modal">
                <div className="modal-header">
                    <h2>Kullanc Ekle</h2>
                </div>
                <div className="modal-content">
                    <input
                        type="text"
                        name="name"
                        placeholder="sim"
                        value={formData.name}
                        onChange={handleInputChange}
                    />
                    <input
                        type="email"
                        name="email"
                        placeholder="Email"
                        value={formData.email}
                        onChange={handleInputChange}
                    />
                    <input
                        type="text"
                        name="username"
                        placeholder="Kullanc Ad"
                        value={formData.username}
                        onChange={handleInputChange}
                    />
                    <Dropdown
                        options={roleOptions}
                        onChange={handleRoleChange}
                    />
                    <input
                        type="text"
                        name="company_name"
                        placeholder="irket Ad"
                        value={formData.company_name}
                        onChange={handleInputChange}
                    />
                </div>
                <div className="modal-footer">
                    <button className="btn btn-submit" onClick={handleSubmit}>Submit</button>
                    <button className="btn btn-cancel" onClick={onCancel}>Cancel</button>
                </div>
            </div>
        </div>
    )
}


